<template>
  <div class="sl-errtip" v-if="visible">
      <div class="sl-dialog-content" :class="{'sl-dialog-big':big}">
        {{ message }}
      </div>
  </div>
</template>

<script>
// this.$slnotify({
//   message: ""
// });
export default {
  data() {
    return {
      visible: false,
      message: "",
      duration: 2000,
      big: false,
      timer: null
    };
  },
  mounted() {
    if (this.duration > 0) {
      this.timer = setTimeout(() => {
        this.visible = false;
      }, this.duration);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.sl-errtip {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999;
}
.sl-dialog-content {
  @include wh(360px, 100px);
  opacity: 0.8;
  background: #000000;
  color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999;
  border-radius: 2px;
  text-align: center;
  line-height: 100px;
  font-size: 14px;
}
.sl-dialog-big {
  height: 250px;
  top: 58%;
  line-height: 250px;
}
</style>




