<template>
  <div class="change-password">
      <el-dialog
        :visible.sync="isDialogVisible"
        :before-close="handleClose"
        :show-close="false"
        @open="init"
        size="">
        <div class="dialog-title" slot="title"></div>
        <div class="dialog-content">
            <el-form class="change-form" :model="changepwd" label-width="100px">
                <el-form-item label="">
                    <el-input 
                    :class="{errtip:isShowoldErr}" 
                    v-model="changepwd.old_password"
                    :type="pwdType"
                    @change="clearPasswordtip"
                    ></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input 
                    v-model="changepwd.new_password"
                    :class="{errtip:isShownewErr}"
                    :type="pwdType"
                    @change="clearPasswordtip"  
                    ></el-input>
                </el-form-item>
                <el-form-item label="" class="last">
                    <el-input 
                    v-model="changepwd.new_password_again"
                    :class="{errtip:isShownewErr}"
                    :type="pwdType"
                    @blur="checkPassword"
                    @change="clearPasswordtip" 
                    ></el-input>
                </el-form-item>
                
                <div class="errtip-text">{{ errtipText }}</div>
                
                <div class="btn-group">
                    <div class="btn btn-confrim" @click="confirmChagne"></div>
                   <div class="btn btn-cancel" @click="handleClose"></div>
                </div>
            </el-form>
        </div>
      </el-dialog>

  </div>
</template>

<script>
import { changePassword } from "../api";
import { errcode } from "@/config/cfg";

export default {
  data() {
    return {
      isDialogVisible: false,

      changepwd: {
        old_password: "",
        new_password: "",
        new_password_again: ""
      },

      isShowoldErr: false,
      isShownewErr: false,

      pwdType: "password",

      errtipText: ""
    };
  },
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    dialogVisible: function(val) {
      if (val === true) {
        this.isDialogVisible = true;
      }
    }
  },
  methods: {
    handleClose() {
      this.$emit("on-close");
      this.isDialogVisible = false;
    },
    handleSucClose() {
      this.$emit("on-scuclose");
      this.isDialogVisible = false;
    },
    init() {
      this.changepwd.old_password = "";
      this.changepwd.new_password = "";
      this.changepwd.new_password_again = "";
      this.clearPasswordtip();
    },
    checkPassword() {
      if (this.changepwd.new_password !== this.changepwd.new_password_again) {
        this.errtipText = "";
        this.isShownewErr = true;
      } else {
        this.isShownewErr = false;
      }
    },
    confirmChagne() {
      if (
        this.changepwd.old_password === "" ||
        this.changepwd.new_password === "" ||
        this.changepwd.new_password_again === ""
      ) {
        return;
      }
      let data = {
        save_password: 1,
        old_password: this.changepwd.old_password,
        new_password: this.changepwd.new_password,
        new_password_again: this.changepwd.new_password_again
      };

      changePassword(data, resp => {
        if (resp.ret === 0) {
          console.log("");
          this.handleSucClose();
        } else if (resp.ret === errcode.USER_PASSWD_ERR) {
          this.errtipText = "";
        } else {
          console.warn("");
        }
      });
    },
    clearPasswordtip() {
      this.isShowoldErr = false;
      this.isShownewErr = false;
      this.errtipText = "";
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.dialog-title {
  @include wh(540px, 40px);
  @include fc(16px, #fff);
  background-color: #5a8cff;
  text-align: center;
  line-height: 40px;
}
.errtip-text {
  @include fc(12px, #e7487e);
  height: 66px;
  padding-left: 100px;
  line-height: 34px;
}
.btn-group {
  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 56px;
  }
}
.dialog-content {
  padding: 40px 80px 50px 80px;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";
.change-password {
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
  .el-input .el-input__inner {
    height: 34px;
    border-radius: 0;
  }
  .el-form-item__label {
    height: 34px;
  }
  .el-input.errtip .el-input__inner {
    border-color: #e7487e;
  }
  .el-form-item {
    margin-bottom: 20px;
  }
  .el-form-item.last {
    margin-bottom: 0;
  }
}
</style>



