<template>
  <section class="app-main">
    <transition name="fade" mode="out-in">
      <router-view :key="key" @creatshop-sucess="creatshopScu"></router-view>
    </transition>
  </section>
</template>

<script>
export default {
  name: "AppMain",
  computed: {
    key() {
      return this.$route.name !== undefined
        ? this.$route.name + +new Date()
        : this.$route + +new Date();
    }
  },
  methods: {
    creatshopScu() {
      this.$emit("creatshop-sucess");
    }
  }
};
</script>

<style lang="scss" scoped>
.app-main {
  padding: 0 14px 50px 14px;
}
</style>




