<template>
    <div id="Navbar">
        <nav class="nav clearfix">
            <!--  -->
            <div v-if="isShowsSidebar" class="menu-btn left" @click="toggleSide" :class="{hide: isHideSlide}">
                <img src="./images/menu_buttonl.png" >
            </div>
            <!--  -->
            <div v-if="!isShowsSidebar" class="menu-btn left">
                <img src="./images/menu_buttonl.png" >
            </div>

            <div class="exit-content right">
                <el-tooltip class="item" effect="dark" content="" placement="bottom">
                  <img @click="goPersonalcenter" src="./images/person.png">
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="" placement="bottom">
                  <img @click="goMessage" src="./images/message.png">
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="" placement="bottom">
                  <img @click="openChangePassword" src="./images/change.png">
                </el-tooltip>
                <el-tooltip class="item" effect="dark" content="" placement="bottom">
                  <img @click="layoutopen" src="./images/exit.png">
                </el-tooltip>
            </div>
        </nav>
        <transition name="fadeimg">
            <div class="banner" v-if="isShowBanner">
                <div>
                    <img class="banner-image" src="./images/banner.png" alt="banner">                    
                    <span class="close-btn" @click="closeBanner">
                        <img src="./images/close.png" alt="close">
                    </span>
                </div>
            </div>
        </transition>

        <!--  -->
        <change-password 
        :dialogVisible="dialogVisible" 
        @on-close="closeChangePassword"
        @on-scuclose="changePwdSuc"></change-password>

        <!--  -->
        <el-dialog :visible.sync="sucPwdDialogVisible"
        :show-close="false"
        class="success-dialog"
        size="">
          <div class="dialog-title"></div>
          <div class="dialog-content">
            <div class="success-tip">
              <div class="success-img">
                <img src="./images/success_img_normal.png">
              </div>
              <div class="success-text"></div>
            </div>

            <div class="button-group clearfix">
                <div class="ok-btn left" @click="successHandler"></div>
            </div>
          </div>
        </el-dialog>

        <!--  -->
        <el-dialog :visible.sync="logoutDialogVisible"
        :show-close="false"
        class="layout-dialog"
        size="">
          <div class="dialog-title"></div>
          <div class="dialog-content">
            <div class="success-tip">
              <div class="success-img">
                <img src="./images/question_img_normal.png">
              </div>
              <div class="success-text"></div>
            </div>

            <div class="button-group clearfix">
                <div class="ok-btn left" @click="layoutHandler"></div>
                <div class="cancel-tbn left" @click="layoutcancel"></div>
            </div>
          </div>
        </el-dialog>

    </div>
</template>

<script>
import ChangePassword from "../components/changePasswordDialog";
import Cookies from "js-cookie";

import { logout } from "../api";
import { ls } from "@/config/pageStore";

export default {
  components: {
    ChangePassword
  },
  props: {
    isShowsSidebar: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      isHideSlide: false,
      isShowBanner: true,

      dialogVisible: false,

      sucPwdDialogVisible: false,

      logoutDialogVisible: false
    };
  },
  methods: {
    toggleSide() {
      this.$emit("change-sidebar");
      this.isHideSlide = !this.isHideSlide;
    },
    closeBanner() {
      this.isShowBanner = false;
    },
    //
    logoutaction() {
      let data = {
        logout: 1
      };
      logout(data, resp => {
        if (resp.ret === 0) {
          ls.removeItem("public#userid");
          ls.removeItem("public#shopinfo");
          ls.removeItem("public#shopid");
          ls.removeItem("SELECTMENU");
          Cookies.remove("token");

          this.$router.push("/");
        } else {
          console.warn("");
        }
      });
    },
    goMessage() {
      this.$router.push("/systemmsgmenu/systemmsg");
      this.$store.commit("CHANGE_SELECTMENU", "/systemmsgmenu/systemmsg");
    },
    goPersonalcenter() {
      this.$router.push("/personalcenter/userinfo");
      this.$store.commit("CHANGE_SELECTMENU", "/personalcenter/userinfo");
    },
    openChangePassword() {
      this.dialogVisible = true;
    },
    closeChangePassword() {
      this.dialogVisible = false;
    },
    successHandler() {
      this.sucPwdDialogVisible = false;
    },
    changePwdSuc() {
      this.dialogVisible = false;
      setTimeout(() => {
        this.sucPwdDialogVisible = true;
      }, 500);
    },
    //
    layoutcancel() {
      this.logoutDialogVisible = false;
    },
    layoutopen() {
      this.logoutDialogVisible = true;
    },
    layoutHandler() {
      this.logoutaction();
      this.logoutDialogVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.nav {
  height: 60px;
  margin-bottom: 14px;
  padding: 20px 14px 14px;
  background-color: #5a8cff;

  .menu-btn {
    @include wh(20px, 20px);
    cursor: pointer;
  }
  .menu-btn.hide {
    transition: all 0.3s ease-out;
    transform: rotate(90deg);
  }
  .exit-content {
    img {
      cursor: pointer;
      @include wh(16px, 16px);
      margin-right: 14px;
    }
    img:last-child {
      margin-right: 26px;
    }
  }
}
.banner {
  height: 156px;
  padding: 0 14px 14px;

  div {
    @include wh(100%, 100%);
    position: relative;

    img.banner-image {
      @include wh(100%, 100%);
    }
    .close-btn {
      @include wh(30px, 30px);
      position: absolute;
      top: 0;
      right: 0;
      cursor: pointer;
    }
  }
}
.fadeimg-enter-active,
.fadeimg-leave-active {
  transition: all 0.3s ease-out;
}
.fadeimg-enter,
.fadeimg-leave-to {
  height: 0;
  padding: 0 14px;
}
.success-dialog {
  .dialog-title {
    @include wh(440px, 4px);
    background-color: #5a8cff;
  }
  .dialog-content {
    padding: 80px 140px 50px;

    .success-img {
      display: inline-block;
      @include wh(50px, 50px);
      img {
        @include wh(50px, 50px);
      }
    }
    .success-text {
      display: inline-block;
      padding-bottom: 15px;
      padding-left: 20px;
      vertical-align: bottom;
      @include fc(14px, #666);
    }
  }

  .button-group {
    margin-top: 40px;

    div {
      @include wh(160px, 40px);
      border-radius: 4px;
      border: 1px solid #5a8cff;
      color: #5a8cff;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
    .ok-btn {
      background-color: #5a8cff;
      color: #fff;
    }
  }
}
.layout-dialog {
  .dialog-title {
    @include wh(540px, 4px);
    background-color: #5a8cff;
  }
  .dialog-content {
    padding: 80px 75px 50px;

    .success-img {
      display: inline-block;
      @include wh(50px, 50px);
      img {
        @include wh(50px, 50px);
      }
    }
    .success-text {
      display: inline-block;
      padding-bottom: 15px;
      padding-left: 20px;
      vertical-align: bottom;
      @include fc(14px, #666);
    }
    .success-tip{
      padding-left: 100px;
    }
  }

  .button-group {
    margin-top: 40px;

    div {
      @include wh(160px, 40px);
      border-radius: 4px;
      border: 1px solid #5a8cff;
      color: #5a8cff;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
    .ok-btn {
      background-color: #5a8cff;
      color: #fff;
      margin-right: 60px;
    }
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";
#Navbar {
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
}
</style>



