import { Util } from '@/config/util';

/**
 * 
 * @param {*} data 
 * @param {*} callback 
 */
export const logout = function (data, callback) {
    Util.DataEncSubmit('login_save.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};

/**
 * 
 * @param {*} data 
 * @param {*} callback 
 */
export const changePassword = function (data, callback) {
    Util.DataEncSubmit('user_info.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};

/**
 * logoname
 */
export const getLogoandName = function (data, callback) {
    Util.DataEncSubmit('shopinfo_get.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};


