
const good = {
    state: {
        selectCategoryId: "0",  //id,
        type: 1,   //
        pageSize: 10,  //
    },

    mutations: {
        CHANGE_ID: (state, selectCategoryId) => {
            state.selectCategoryId = selectCategoryId;
        },
        CHANGE_TYPE: (state, type) => {
            state.type = type;
        },
        CHANGE_PAGESIZE: (state, pageSize) => {
            state.pageSize = pageSize;
        }
    },

    actions: {}
};

export default good;



