<template>
    <div id="sidebar">
        <div class="sl-icon">
            <img src="./images/logo_normal.png" />
        </div>
        <div class="shop-info">
            <div class="shop-icon">
                <img v-if="stShoplogo === ''" src="./images/personal.png" alt="logo">
                <img v-else
                :src="imgbase_url + '/img_get.php?img=1&height=80&width=80&imgname=' + stShoplogo">
            </div>
            <h2 class="shop-name">
                {{ stShopname }}
            </h2>
        </div>

        <el-menu mode="vertical" 
          @select="selectMenu" 
          unique-opened 
          :default-active="saveSelectMenu">
            <section v-for="(item, index) in routes" 
              :key="index">
                <router-link v-if="!item.hidden&&item.noDropdown&&item.children.length>0" 
                    :to="item.path + '/'+item.children[0].path">
                    <el-menu-item :index="item.path+'/'+item.children[0].path" class="sl-title">
                        <i class="iconfont" :class="item.icon"></i> 
                        <span class="slmenu-name">{{item.name}}</span>
                    </el-menu-item>
                </router-link>

                <el-submenu :index="item.name" v-if="!item.noDropdown&&!item.hidden">
                    <template slot="title">
                        <i class="iconfont" :class="item.icon"></i>
                        <span class="slmenu-name">{{item.name}}</span>
                    </template>

                    <template v-for="child in item.children" v-if="!child.hidden">
                        <router-link class="menu-indent" :to="item.path+'/'+child.path">
                            <el-menu-item :index="item.path+'/'+child.path">
                                <span class="slmenu-name">{{child.name}}</span>
                            </el-menu-item>
                        </router-link>
                    </template>
                </el-submenu>
            </section>
        </el-menu>
    </div>
</template>

<script>
import { constantRouterMap } from "@/router/index";
import { ls } from "@/config/pageStore";
import { getLogoandName } from "../api";

export default {
  data() {
    return {
      shopName: "",
      shopImg: "",
      routes: constantRouterMap,
      shopinfo: null,
      shopinfoByid: {},

      saveSelectMenu: "", //menu
      imgbase_url: imgbase_url //
    };
  },
  computed: {
    stShopname() {
      return this.$store.state.login.stshopname;
    },
    stShoplogo() {
      return this.$store.state.login.stshoplogo;
    }
  },
  created() {
    this.getShopinfo();
    this.initSelectMenu();
  },
  methods: {
    getShopinfo() {
      // let shoplist = Public.getShopinfo().shopinfo || [];
      // let shopid = Public.getShopid();

      // shoplist.forEach(item => {
      //   this.shopinfoByid[item.shop_id] = item;
      // });

      // this.shopinfo = this.shopinfoByid[shopid];

      // let shopName = this.shopinfo.shop_name;
      // let shopImg = (this.shopinfo.img_list || [])[0] || "";

      // this.$store.commit("GETSTSHOPLOGO", shopImg);
      // this.$store.commit("GETSTSHOPNAME", shopName);
      getLogoandName({ get_shopinfo_base: 1 }, resp => {
        if (resp.ret === 0) {
          this.shopinfo = resp.data.shopinfo;
          let shopName = this.shopinfo.shop_name;
          let shopImg = this.shopinfo.shop_logo;

          this.$store.commit("GETSTSHOPLOGO", shopImg);
          this.$store.commit("GETSTSHOPNAME", shopName);
        }
      });
    },
    selectMenu(path) {
      this.saveSelectMenu = path;
      ls.setItem("SELECTMENU", this.saveSelectMenu);
    },
    initSelectMenu() {
      let path = ls.getItem("SELECTMENU") || "/statistical";
      this.saveSelectMenu = path;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.sl-icon {
  height: 60px;
  background-color: #4877e7;
  text-align: center;

  img {
    @include wh(106px, 36px);
    margin-top: 12px;
  }
}

.shop-info {
  height: 156px;
  position: relative;

  .shop-icon {
    @include wh(80px, 80px);
    border-radius: 40px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    top: 20px;

    img {
      @include wh(80px, 80px);
      border-radius: 40px;
    }
  }
  .shop-name {
    @include fc(18px, #333);
    font-weight: bold;
    text-align: center;
    height: 56px;
    width: 210px;
    line-height: 56px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.sl-title {
  background-color: #f6f8fc;
}
.slmenu-name {
  color: #333;
  padding-left: 20px;
}
</style>

<style lang="scss">
//element-ui
#sidebar {
  .el-menu-item {
    background-color: #fff;
    height: 40px;
    line-height: 40px;

    .iconfont {
      color: #4877e7;
    }
  }

  .el-submenu__title {
    background-color: #eef1f6;
    height: 40px;
    line-height: 40px;

    .iconfont {
      color: #4877e7;
    }
  }

  .el-menu-item.is-active {
    background-color: #4877e7;
    .iconfont {
      color: #fff;
    }
    .slmenu-name {
      color: #fff;
    }
  }

  .sl-title {
    background-color: rgb(238, 241, 246);
  }
}
</style>





