<template>
    <div class="app-wrapper" :class="{hide:isHideSide,hideSide: !isShowsSidebar}" v-if="isRender">
      <div class="main-left" v-if="isShowsSidebar">
        <sidebar 
        v-if="hidesidebar" 
        class="sidebar-container" 
        :isShowsSidebar="isShowsSidebar"></sidebar>
      </div>
      <div class="main-container">
          <navbar @change-sidebar="toggleSidebar"></navbar>
          <app-main @creatshop-sucess="creatshopSuc"></app-main>
      </div>
    </div>
</template>

<script>
import Sidebar from "./Sidebar";
import AppMain from "./AppMain";
import Navbar from "./Navbar";

import { ls } from "@/config/pageStore";
import { Public } from "@/config/public";

export default {
  components: {
    Sidebar,
    AppMain,
    Navbar
  },
  data() {
    return {
      isHideSide: false,
      hidesidebar: true,

      isShowsSidebar: true, //

      isRender: true
    };
  },
  computed: {
    saveStoreSelectMenu() {
      return this.$store.state.layout.selectMenu;
    }
  },
  watch: {
    saveStoreSelectMenu: function(val) {
      if (val === "/systemmsgmenu/systemmsg") {
        this.saveSelctMenu = val;
        ls.setItem("SELECTMENU", val);
        this.$store.commit("CHANGE_SELECTMENU", "");
        this.rerender();
      } else if (val === "/personalcenter/userinfo") {
        ls.setItem("SELECTMENU", val);
        this.rerender();
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    creatshopSuc() {
      // this.rerenderAll();
      this.$emit("creatshop-sucess");
    },
    toggleSidebar() {
      this.isHideSide = !this.isHideSide;
    },
    //
    rerender: function() {
      // debugger;
      this.hidesidebar = false;
      this.$nextTick(function() {
        this.hidesidebar = true;
      });
    },
    rerenderAll: function() {
      this.isRender = false;
      this.$nextTick(function() {
        this.isRender = true;
      });
    },
    init() {
      let shopinfo = Public.getShopinfo().shopinfo || [];

      if (shopinfo.length === 0) {
        this.isShowsSidebar = false;
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.app-wrapper {
  position: relative;
  @include wh(100%, 100vh);

  .sidebar-container {
    width: 210px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 1001;
    overflow-y: auto;
    transition: all 0.3s ease-out;
    box-shadow: 0 2px 4px 0 #becaeb;
    // border-right: 1px solid #ccc;
  }

  .main-container {
    min-height: 100%;
    transition: margin-left 0.3s ease-out;
    margin-left: 210px;
    background-color: #eff3fa;
  }
}
.app-wrapper.hide {
  .sidebar-container {
    width: 0;
  }
  .main-container {
    margin-left: 0;
  }
}
.app-wrapper.hideSide {
  .sidebar-container {
    width: 0 !important;
  }
  .main-container {
    margin-left: 0 !important;
  }
}
</style>




