// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import locale from 'element-ui/lib/locale/lang/zh-CN';

import '@/assets/font/iconfont.css';
import '@/styles/common.scss';

import App from './App';
import router from './router';
import store from './store';

import '@/config/utilPageStore';
import '@/config/jquery.md5';
import '@/config/jsencrypt';

import { Util } from '@/config/util.js';
import { Public } from "@/config/public.js";


//
import SlNotification from '@/components/notification/index.js';
Vue.component(SlNotification.name, SlNotification);
Vue.prototype.$slnotify = SlNotification;

Vue.use(ElementUI, { locale });
window.router = router;

Vue.config.productionTip = false;

//
router.beforeEach((to, from, next) => {
    let userid = Public.getUserid();

    if (to.path !== "/" && to.path !== '/forgetpassword' && to.path !== '/registrate') {
        if (userid && userid > 0) {
            next();
        } else {
            next('/');
        }
    } else {
        next();
    }

});

Util.creatKey();
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
});






