<template>
  <div id="app">
   <!-- keepalive -->
    <!-- <transition name="fade" mode="out-in">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition> -->

    <!-- keepalive -->
    <transition name="fade" mode="out-in">
      <router-view @creatshop-sucess="rerenderAll" v-if="isShow"></router-view>
    </transition>
  
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      isShow: true
    };
  },
  methods: {
    rerenderAll: function() {
      this.isShow = false;
      this.$nextTick(function() {
        this.isShow = true;
      });
    }
  },
  computed: {
    key() {
      return this.$route.name !== undefined
        ? this.$route.name + +new Date()
        : this.$route + +new Date();
    }
  }
};
</script>

<style scoped lang="scss">
</style>



