
const login = {
    state: {
        shopinfo: [],  //  
        userid: "",   //userid
        shopid: "",   //shopid
        stshoplogo: "", //shoplogo
        stshopname: "", //shopname
    },

    mutations: {
        CHANGE_SHOPINFO: (state, shopinfo) => {
            state.shopinfo = shopinfo;
        },
        CHANGE_USERID: (state, userid) => {
            state.userid = userid;
        },
        SHOP_USERID: (state, shopid) => {
            state.shopid = shopid;
        },
        GETSTSHOPLOGO: (state, shoplogo) => {
            state.stshoplogo = shoplogo;
        },
        GETSTSHOPNAME: (state, shopname) => {
            state.stshopname = shopname;
        }
    },

    actions: {}
};

export default login;



